
.pm__header-version_two {
    margin-top: 4rem;
    display: flex;
    background-color: white;
    padding: 2% 5%;
    
    /* min-height: 100vh; */
    .pm__header-content {
        flex: 1;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        max-width: 75%;
        padding-left: clamp(0px, 30px, 40px);
        margin-right: 10%;
    
    }
    
    .pm__header-content h1 {
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 60px;
    
    }
    
    .pm__header-content p {
        margin: 3rem 0rem;
        font-family: var(--font-family);
        font-weight: 300;
        font-size: 22px;
        line-height: 25px;
        margin-right: 0;
    
        
        animation-delay: 0.05s;
    }
    
    
    .pm__header-content button {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 15px;
        background-color: #D9D9D9;
        color: var(--color-accent-red);
        border: none;
        padding: 10px 50px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        margin: 4px 2px;
        cursor: pointer;
        border-radius: 25px;
        outline: 2px solid var(--color-accent-red);
        animation-delay: 0.08s;
    
        display: none;
    }
    
    .pm__header-content button:hover {
        background-color: var(--color-accent-red);
        color: #D9D9D9;
    }
    
    .pm__header-image {
        flex: 1;
        display: flex;
        justify-content: right;
        align-items: center;
        max-width: 35%;
        @media screen and (max-width: 1050px) {
            max-width: 100%;
        }
    }
    
    .pm__header-image img {
        max-width: 100%;
        height: auto;
        animation-delay: 0.08s;
        margin-bottom: 1rem;
    }
    
}

@media screen and (max-width: 650px) {
    /* .pm__header h1 {
        margin-right: 0;
        font-size: 48px;
        line-height: 60px;
    } */
    .pm__header-content p {
        font-size: 18px;
        line-height: 24px;
        margin-right: 0;
    }
    .section_margin {
        margin: 0;
    }
}

@media screen and (max-width: 450px) {
    .pm__header-version_two h1 {
        font-size: 40px;
        line-height: 60px;;
    }
    .pm__header-content p {
        font-size: 16px;
        margin: 1.5rem 0rem;
    }
}

@media screen and (max-width: 1050px) {
    .pm__header-version_two {
        flex-direction: column;
    }
    .pm__header-content {
        margin: 0 0 3rem;
    }

}

